<template>
  <a :href="project.website ? project.website : '#'" class="project__anchor">
    <div class="tile__wrapper" :class="{'tile--lg': sizeLg}" :style="styleObject">
      <div class="tile__banner" :class="{'banner--lg': sizeLg}">
        <img draggable="false" class="logo__img" :src="project.bannerLogo ? require(`@/assets/${project.bannerLogo}`) : ''" alt="">
        <img draggable="false" class="banner__img" :src="require(`@/assets/${project.bannerImg}`)" alt="">
      </div>

      <h3 class="tile__title">{{ project.name }}</h3>
      <p class="tile__description">{{ project.description }}</p>

    </div>
  </a>
</template>


<script>
export default {
  name: 'ProjectTile',

  props: {
    size: {
      type: String,
      default: 'md',
      validator(v) {
        return ['lg', 'md'].includes(v);
      },
    },
    project: {
      type: Object,
      required: true,
    },
  },

  data() {
    let startColor = null;
    let endColor = null;
    let bannerLogo = null;

    if (this.project.theme && this.project.theme.gradient) {
      [startColor, endColor] = this.project.theme.gradient;
    }

    if (this.project.bannerLogo) {
      bannerLogo = `url("../assets/${this.project.bannerLogo}")`;
    }

    return {
      styleObject: {
        '--bannerLogo': bannerLogo,
        '--gradientStartColor': startColor,
        '--gradientEndColor': endColor,
      },
    };
  },

  computed: {
    sizeLg() {
      return this.size === 'lg';
    },
  },
};
</script>


<style scoped>
.project__anchor {
  text-decoration: none;
  color: #444;
}

.tile__wrapper {
  padding: 1.2em;
  border-radius: 0.2em;
  border: 1px solid transparent;
  transition: all 0.2s ease-in-out;
}

.tile--lg {
  padding: 1.2em;
}

.tile__wrapper:hover {
  background: rgba(143, 172, 192, 0.2);
  cursor: pointer;
}

.tile__banner {
  pointer-events: none;
  user-select: none;
  background: rgba(143, 172, 192, 0.2);
  border-radius: 0.2em;
  height: 8em;
  overflow: hidden;
  position: relative;
  width: 100%;
}

.banner--lg {
  height: 10em;
}

.logo__img {
  height: 3.5em;
  left: 50%;
  position: absolute;
  top: 50%;
  width: 3.5em;
  transform: translateX(-50%) translateY(-50%);
  filter: drop-shadow(0.1em 0.1em 0.1em rgba(51, 51, 51, 0.4));
}

.banner__img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.tile__description {
  margin: 0;
}

.tile__banner::before {
  content: '';
  height: 100%;
  width: 100%;
  left: 0;
  position: absolute;
  top: 0;
  background: var(--gradientEndColor);
  background: -webkit-linear-gradient(to right, var(--gradientStartColor), var(--gradientEndColor));
  background: linear-gradient(to right, var(--gradientStartColor), var(--gradientEndColor));
  opacity: 0.7;
}
</style>
